
body {
  margin: 0;
  color: #001e00;
  letter-spacing: 0.4px;
  font-family:Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
}

.app {
  display: flex;
  flex-direction: column;

  &__header {
    position: sticky;
    left: 0;
    right: 0;
    top: 0;
  }

  &__subnav {
    position: sticky;
    top: 64px;
  }

  &__main {
    box-sizing: border-box;
    margin: 0 auto;
    padding: 0 64px 100px;
    max-width: 1600px;
  }

  &__rebrandhero {
    margin: 41px 0 80px;
  }

  &__howtohire {
    margin: 0 0 64px;
  }

  &__category {
    margin: 0 0 80px;
  }

  &__testimonials{
    margin-top: 85px;
  }

  &__ukraine {
    margin-top: 100px;
  }

  &__footer {
    align-self: center;
  }
}